import { Link } from 'react-router-dom';
import CardCurve from '../assets/dynamic/card-curve';
import Placeholder from '../assets/placeholder.svg'

const Card = ({ color, item }) => (
  <Link to={`/${item.collection}/${item.category}`}>
    <div className="card">
      {/* TODO: update image on hover */}
      <img
        alt={`${item.category} from the ${item.collection} collection`}
        className="card-image"
        onError={(e) => e.target.src = Placeholder}
        src={`${process.env.PUBLIC_URL}/media/products/${item.category}-${item.collection}.jpeg`}
      />
      <h2 className="card-name">{item.name}</h2>
      <CardCurve color={color} />
      <div className="card-ratio"></div>
    </div>
  </Link>
);

export default Card;
