import { Routes, Route, Link } from "react-router-dom";
import logo from '../assets/logo.svg';
import HeaderAnimated from './HeaderAnimated';

const Header = ({ color }) => {
  return (
    <div className="header">
      <Routes>
        <Route path="/" element={(<HeaderAnimated color={color} />)} />
        <Route path="*" element={(
          <div className={`header-small center-children bg bg-color-${color} bg-image-white`}>
            <Link to="/">
              <img src={logo} className="logo-small" alt="logo" />
            </Link>
          </div>
        )} />
      </Routes>
    </div>
  );
};
export default Header;

